.footer {
  background-color: $colordarkblue;

  /*height: 20.06rem;*/
}

.footer-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: $font-main;
  @media all and (min-width: 48rem) {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-start;
    justify-content: flex-end;
    // border: solid 2px red;
    // padding-left: 39px;
    // padding-right: 39px;
    width: 39.37rem;
    margin-left: auto;
    margin-right: auto;
  }
  @media all and(min-width:75rem) {
    margin-right: 23.12rem;
    margin-left: 23.21rem;
  }
}
.footer-nav1 {
  // border: 2px red solid;
  /*margin-top: 60px;*/
  text-align: center;
  margin-bottom: 1.87rem;
  @media all and(min-width:48rem) {
    width: 13.12rem;
    text-align: left;
    margin-left: 1.87rem;
    margin-right: 1.87rem;
  }
}

.footer-nav1-list {
  list-style-type: none;
  line-height: 1.88;
}

.footer-nav-link {
  color: $colorwhite;
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
  line-height: 1.88;
  &:hover {
    color: $colororange;
    transform: scale(2);
  }
}
// .footer-nav-link:hover {
//   color: $colororange;
//   transform: scale(2);
// }

.footer-nav2 {
  // border: 2px red solid;
  width: 9.19rem;
  height: 5.62rem;
  text-align: center;
  margin-bottom: 1.87rem;
  @media all and (min-width: 48rem) {
    order: -1;
    width: 13.12rem;
    text-align: left;
  }
}
.footer-nav2-list {
  list-style-type: none;
  line-height: 1.88;
}
.copy {
  // border: 2px red solid;
  color: $colorwhite;
  font-size: 0.87rem;
  text-align: center;
  margin-bottom: 2.81rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.copy-icon {
  padding-bottom: 0.94rem;
  @media all and (min-width: 48rem) {
    width: 210px;
    text-align: left;
  }
}

.copy-text {
  @media all and (min-width: 48rem) {
    width: 13.12rem;
    text-align: left;
  }
}

