.footer-button {
  background-color: $colorwhite;
  box-shadow: 0.12rem 0.12rem 0.31rem $colordarkgrey;
  width: 3.12rem;
  height: 3.12rem;
  border-radius: 1.87rem;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  top: -1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
@keyframes latidos {
  from {
    transform: none;
  }
  50% {
    transform: scale(1.4);
  }
  to {
    transform: none;
  }
}

.footer-button-top {
  display: block;
  width: 1.28rem;
  height: 1.5rem;
  background-image: url(../images/ico-arrow.svg);
  background-size: contain;
  animation: latidos 0.5s infinite;
  transform-origin: center;
}

// .footer-button-top:hover {
//   transform: scale(2);
// }

@keyframes latidos {
  from {
    transform: none;
  }
  50% {
    transform: scale(1.4);
  }
  to {
    transform: none;
  }
}

.love {
  display: inline-block;
  font-size: 0.62rem;
  text-shadow: 0 0 0.62rem $coloricegrey, 0.06rem 0.06rem 0 $colordarkgrey;
  color: $colororange;
  animation: latidos 0.5s infinite;
  transform-origin: center;
}
